.container>.main,
.container>.list {
  margin-top: 70px;
}

.head {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  text-align: center;
  border-top: 1px solid rgba(34, 36, 38, .15);
  border-bottom: 1px solid rgba(34, 36, 38, .15);
  box-shadow: 0 1px 2px 0 rgb(34 36 38 / 15%);
}

h1 {
  font-size: 23px;
  line-height: 50px;
}

h2 {
  font-size: 18px;
}

h2.right {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.center-div {
  text-align: center;
}